.containers {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 1rem;
	gap: 0.5rem;
}

.containers a {
	border: 1px solid black;
	padding: 1rem;
	border-radius: 5px;
	text-decoration: none;
	font-weight: bold;
	font-size: 1.5rem;
	background-color: cornsilk;
}

.containers a:hover {
	background-color: aliceblue;
}
